* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Raleway";
}

:root {
  --main-color: #16a085;
}
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

body {
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.5;
  color: var(--main-color);
  background-color: var(--main-color);
}

.quote {
  max-width: 700px;
  min-height: 350px;
  margin: 100px auto;
  background-color: #ffffff;
  border-radius: 5px;
  padding: 30px;
}

blockquote {
  text-align: center;
}

.image {
  position: relative;
  margin: auto;
  margin-top: -105px;
  margin-bottom: 20px;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  left: calc(50% - 75px);
}

.text {
  margin: 0px 20px;
  word-wrap: normal;
}

.author {
  font-size: 60%;
  font-weight: 300;
  line-height: 1.4;
  text-align: right;
  margin-right: 50px;
}

.buttons {
  text-align: center;
  position: absolute;
  top: 500px;
  left: calc(50% - 200px);
  width: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.next,
.tweet {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  padding: 0.375rem 0.75rem;
  font-size: 1.2rem;
  line-height: 1.5;
  border: solid 1px transparent;
  color: var(--main-color);
  background-color: #ffffff;
}
.next:hover,
.tweet:hover {
  background-color: var(--main-color);
  color: #ffffff;
  border: solid 1px #ffffff;
}

.next {
  width: 60%;
  cursor: pointer;
  outline-color: var(--main-color);
}

.tweet {
  width: 35%;
  text-decoration: none;
}
#ghanmi {
  color: #ffffff;
  width: 150px;
  text-align: center;
  font-size: 0.7rem;
  font-weight: 300;
  position: absolute;
  bottom: 5px;
  left: calc(50% - 75px);
}

/* RESPONSIVE */

@media screen and (max-width: 600px) {
  .quote {
    width: 90%;
    padding: 30px 5px;
  }
  body {
    font-size: 1.5rem;
  }
  .buttons {
    -ms-flex-pack: center;
        justify-content: center;
    top: 475px;
  }

  .next {
    font-size: 1rem;
    margin: 5px;
    width: 200px;
  }

  .tweet {
    font-size: 1rem;
    margin: 5px;
    width: 100px;
  }
}



